/* harmony default export */ __webpack_exports__["default"] = ("<nav class=\"nav navbar \">\r\n        <div class=\"  col-md-3 col-sm-5 col-xs-6\">\r\n          <a routerLink = \"/home\">\r\n          <img src=\"../assets/img/CNEUF1.png\" class=\"img-responsive logo\">\r\n        </a>\r\n        </div>\r\n        <div  class=\" col-md-2 col-xs-5 pull-right cnx\" *ngIf=\"!isLoggedIn\">\r\n           <a href=\"https://auth-b2c.auf.org/auth/?authorize&client_id=11_5x39q6e96s080cogggk4wos0gcsksoogcs0g80kkgsws0g0oow&return_url=http%3A%2F%2Flocalhost%3A4200%2F%23%2FsignIn\" >Se connecter</a>\r\n           <br>\r\n           Pas encore inscrit ?\r\n          <a href=\"https://auth-b2c.auf.org/auth/?authorize&client_id=11_5x39q6e96s080cogggk4wos0gcsksoogcs0g80kkgsws0g0oow&return_url=http%3A%2F%2Flocalhost%3A4200%2F%23%2FsignIn\">Inscrivez-vous </a>\r\n          </div>\r\n            <div  class=\"  hidden-xs test cnx pull-right \" *ngIf=\"isLoggedIn\">\r\n                <div class=\"row profil\">\r\n               <ul class=\" pull-right\">\r\n                   <li class=\"dropdown\"><a href=\"#\" class=\"dropdown-toggle\" data-toggle=\"dropdown\">\r\n                    <img class=\"img-responsive user\" src=\"../assets/img/user.png\"> &nbsp;&nbsp; Bienvenue {{username}} <b class=\"caret\"></b></a>\r\n                       <ul class=\"dropdown-menu\">\r\n                           <li><a routerLink =\"/admin/dashboardAdmin\" *ngIf= \" userRole === 'CNEUF_ADMIN' \"><i class=\"fas fa-user\"> </i> Mon Espace</a></li>\r\n                           <li><a routerLink = \"/user/MonTableauDeBord\" *ngIf= \" userRole === 'CNEUF_COORDONNATEUR' \"><i class=\"fas fa-user\"> </i> Mon Espace</a></li>\r\n                           <li><a routerLink = \"/user/MonTableauDeBord\" *ngIf= \" userRole === 'CNEUF_EXPERT' \"><i class=\"fas fa-user\"> </i> Mon Espace</a></li>\r\n      \r\n                           <li> <a href=\"https://auth-b2c.auf.org/auth/?authorize&logout&client_id=11_5x39q6e96s080cogggk4wos0gcsksoogcs0g80kkgsws0g0oow&return_url=http%3A%2F%2Flocalhost%3A4200%2F%23%2F\"  (click)=\"logout()\"><i class=\"fas fa-sign-out-alt\"></i>  Se déconnecter</a> </li>\r\n                       </ul>\r\n                   </li>\r\n               </ul>\r\n      \r\n      \r\n                </div>\r\n      \r\n           </div>\r\n      \r\n           <div  class=\"visible-xs test cnx pull-right \" *ngIf=\"isLoggedIn\">\r\n                   <div class=\"row profil\">\r\n                           <ul class=\" pull-right\">\r\n                               <li class=\"dropdown\"><a href=\"#\" class=\"dropdown-toggle\" data-toggle=\"dropdown\">\r\n                                <img class=\"img-responsive user\" src=\"../assets/user.png\">  <b class=\"caret\"></b></a>\r\n                                   <ul class=\"dropdown-menu\">\r\n                                      <li><a routerLink =\"/admin\" *ngIf= \" userRole === 'CNEUF_ADMIN' \"><i class=\"fas fa-user\"> </i> Mon Espace</a></li>\r\n                           <li><a routerLink = \"/Coord\" *ngIf= \" userRole === 'CNEUF_COORDONNATEUR' \"><i class=\"fas fa-user\"> </i> Mon Espace</a></li>\r\n                           <li><a routerLink = \"/expert\" *ngIf= \" userRole === 'CNEUF_EXPERT' \"><i class=\"fas fa-user\"> </i> Mon Espace</a></li>\r\n                                       <li> <a (click)= \" logout()\"><i class=\"fas fa-sign-out-alt\" ></i>  Se déconnecter</a> </li>\r\n                                   </ul>\r\n                               </li>\r\n                           </ul>\r\n      \r\n      \r\n                            </div>\r\n      \r\n           </div>\r\n      </nav>\r\n      ");

