/* harmony default export */ __webpack_exports__["default"] = ("<app-header></app-header>\r\n<div class=\"homeHeader\">\r\n  <div class=\"container\">\r\n    <h2>CAMPUS NUMERIQUES DE L'ESPACE UNIVERSITAIRE FRANCOPHONE</h2>\r\n  </div>\r\n</div>\r\n<img src=\"../assets/sigle_auf.png\" class=\"img-responsive sigle pull-right\">\r\n<br>\r\n <div  class=\"container\"><h3>C'est quoi un CNEUF</h3>\r\n<div class=\"container bodyuser\">\r\n  <div class=\"col-md-6\">\r\n    <p class=\"descrip\">\r\n    Au début des années 1990, l’AUF a soutenu la mise en place au sein\r\n    de ses universités membres d’infrastructures spécifiques, les\r\n    campus numériques francophones (CNF) qui permettaient aux acteurs\r\n    universitaires d’accéder via Internet à des bibliothèques\r\n    numériques, à des formations diplômantes et à distance, et à\r\n    des parcours de soutien aux formateurs dans les domaines des\r\n    TICE. De nouveaux besoins du monde universitaire ont ensuite\r\n    conduit à un élargissement des champs d’activité de proximité.\r\n    Les nouveaux campus numériques francophones « CNEUF » reposent\r\n    sur un agencement des trois dimensions de l’acquisition des\r\n    compétences : acquisition des savoirs, acquisition des savoir-faire,\r\n    acquisition des savoir-être.\r\n    </p>\r\n    <br>\r\n    <p>\r\n      <b>\r\n        Ainsi, les CNEUF offrent aujourd’hui un environnement de travail privilégié avec la mise à disposition :\r\n      </b>\r\n    </p>\r\n    <br>\r\n    <p  class=\"descrip\">\r\n      <li>D’infrastructures techniques et de réseau de qualité pour se former, se diplômer à distance ou encore accéder à des ressources documentaires, pédagogiques, scientifiques, culturelles.</li>\r\n      <li>D’espace de lieu de travail partagé (coworking) pour travailler et partager ses compétences.</li>\r\n      <li>D’accompagnement dans la maturation de projets de fabrication numériques.</li>\r\n    </p>\r\n  </div>\r\n\r\n  <div class=\"col-md-6\">\r\n    <div class=\"rightside\">\r\n    <img src=\"../assets/CNEUF-BRAZAVILLE.jpg\" class=\"img-responsive\">\r\n    <br><br>\r\n    <strong>L’AUF soutient la mise en place de CNEUF via des appels à projets</strong>\r\n    <h4>Présentation d'un CNEUF</h4>\r\n    <a class=\"btn btn1\" href=\"assets\\C-NEUF-V2.pdf\" download=\"C-NEUF-V2.pdf\"> <i class=\"fas fa-download\"></i> Voir la présentation</a>\r\n  </div>\r\n</div>\r\n</div>\r\n</div>\r\n<br>\r\n<div  class=\"container\"><h3>Modalité des canditatures </h3>\r\n<div class=\"container bodyuser\">\r\n    <h3>1. CADRE GÉNÉRAL ET CONTEXTE DE L’APPEL À PROJETS</h3>\r\n    <p class=\"descrip\">\r\n            Au service de ses membres, l’Agence s’est efforcée au fil du temps de répondre à leurs\r\n            attentes à travers des activités menées par des implantations de proximité. Parmi celles-ci,\r\n            les Campus numériques francophones (CNF) ont eu un rôle novateur majeur.\r\n            Aujourd’hui, ces campus sont appelés – en cohérence avec les nouvelles orientations\r\n            stratégiques de l’Agence - à se renouveler, à reprendre un temps d’avance sur leur\r\n            écosystème qui doit se matérialiser dans une diversification et une rénovation des services\r\n            qu’ils proposent à nos membres, dans le cadre d’une démarche de co-construction\r\n            partenariale.\r\n            Depuis 2010, un ensemble de plans pour l’amélioration et l’évolution des services des CNF\r\n            ont été mis en œuvre, notamment l’élaboration d’une démarche qualité́ ayant permis\r\n            d’identifier et de normaliser les services proposés dans les CNF. S’en est suivi un plan\r\n            d’action progressif avec des phases de sensibilisation et d’accompagnement au\r\n            changement :\r\n        <li>Réunion des responsables de CNF Gennevilliers - décembre 2011</li>\r\n        <li>Réunion des responsables de CNF Gennevilliers - janvier 2015</li>\r\n        <li>Corpus : CNF, futurs tiers-lieux des savoirs – décembre 2015</li>\r\n        <li>Réunion des responsables de CNF et des référents IFIC à Sofia –novembre 2015 </li>\r\n        <li>Diffusion du modèle économique des CNF – janvier 2016</li>\r\n        <li>Création de la communauté́d'innovation – avril 2016</li>\r\n        <li>Mise en place du groupe de travail central – juin 2016</li>\r\n\r\n        Le présent appel à projets donne une suite pratique à ces travaux préparatoires\r\n    </p>\r\n\r\n    <h3>2. LE PROJET C-NEUF</h3>\r\n    <p class=\"descrip\">\r\n            En résumé, le C-NEUF est un projet, mené en partenariat avec des établissements membres\r\n            et des opérateurs socio-économiques, porté par une direction régionale et mis en œuvre par\r\n            une ou plusieurs implantations de proximité. Dans un premier temps, la réponse à l’appel à\r\n            projet doit émaner des CNF.\r\n            Un Projet C-NEUF se pense comme un modèle de tiers-lieu académique, associatif et\r\n            entrepreneurial, accélérateur d’innovations, facteur de développement et d’employabilité. Il\r\n            est composé d’espaces ouverts aux usagers des services proposés par l’AUF et est destiné à\r\n            accueillir de nouveaux publics. Ces espaces sont dotés de ressources numériques, pour la\r\n            formation et la recherche, accessibles à partir d’espaces collaboratifs (physiques ou virtuels).\r\n            Un C-NEUF est destiné́à :\r\n\r\n        <li>La diffusion/l’acquisition des connaissances</li>\r\n        <li>La diffusion/l’acquisition des connaissances</li>\r\n        <li>Corpus : CNF, futurs tiers-lieux des savoirs – décembre 2015</li>\r\n\r\n        <li>La valorisation socio-économique des savoirs, savoir-faire et des savoir-être </li>\r\n        Son essence est l’offre, dans un contexte déterminé, de services innovants variés en\r\n        cohérence avec les orientations stratégiques de l’AUF. Ces services doivent être clairement\r\n        identifiés et leur mise en œuvre décrite avec ses résultats attendus.\r\n        Une description détaillée figure en annexe au présent appel\r\n    </p>\r\n\r\n    <h3>3. CONDITIONS DE CANDIDATURE</h3>\r\n    <p class=\"descrip\">\r\n            Le projet doit :\r\n\r\n        <li>Être porté et présenté par une direction régionale</li>\r\n        <li>Fédérer autour d’un ou plusieurs CNF au moins trois partenaires :\r\n            <ul>\r\n                <li style=\"list-style-type: circle;\">L’AUF </li>\r\n                <li style=\"list-style-type: circle;\">Un partenaire académique</li>\r\n                <li style=\"list-style-type: circle;\">Un autre du milieu socio- économique et/ou associatif</li>\r\n            </ul>\r\n        </li>\r\n        <li>Montrer la participation collective au financement du projet </li>\r\n        <li>Afficher le caractère innovant de la proposition  </li>\r\n        <li>Être dans l’alignement des orientations stratégiques de l’AUF </li>\r\n        <li>Montrer l’insertion de ses activités dans au moins deux des trois espaces\r\n                caractéristiques du C-NEUF  </li>\r\n\r\n    </p>\r\n\r\n    <h3>4. MESURES D’ACCOMPAGNEMENT</h3>\r\n    <p class=\"descrip\">\r\n            Pour le montage des projets, objet de l’actuel appel, les directions du numérique des\r\n            partenariats et de la veille-innovation-expertise, pourront conseiller les directions régionales\r\n            qui les solliciteraient.\r\n            Pour les futurs appels, un séminaire atelier spécifique peut être organisé à la demande des\r\n            directions régionales qui le souhaitent.\r\n    </p>\r\n\r\n    <h3>5. CRITERES DE SELECTION   </h3>\r\n    <p class=\"descrip\">\r\n          <li>Éligibilité : projet porté par une direction régionale et fédérant au moins deux\r\n                partenaires extérieurs à l’AUF. La présence d’acteurs socio-économiques ou\r\n                associatif est demandée</li>\r\n          <li>Alignement à la stratégie AUF : répond à au moins deux des trois défis de la\r\n                stratégie</li>\r\n          <li>Ancrage territorial : originalité́\r\n                locale, spécificité́, complémentarité́, lisibilité́ et\r\n                effet structurant</li>\r\n          <li>Faisabilité́ technique : l’environnement offre les conditions de réalisation\r\n                favorables</li>\r\n          <li>Engagement des partenaires : engagement de soutien humain, matériel ou\r\n                financier chiffré</li>\r\n          <li>Budget : en adéquation avec les objectifs</li>\r\n          <li>Pérennisation : explicitation des conditions de pérennisation post financement\r\n                initial</li>\r\n    </p>\r\n\r\n    <h3>6. ÉLEMENTS BUDGETAIRES</h3>\r\n    <p class=\"descrip\">\r\n            Il s’agit d’un projet en partenariat. Il convient de décrire les apports de la direction régionale\r\n            – s’il y a lieu - de chacun des partenaires et de préciser le complément de financement\r\n            demandé à la direction du numérique.\r\n\r\n    </p>\r\n\r\n  </div>\r\n\r\n</div>\r\n<div  class=\"container\"><h3>Documents de référence </h3>\r\n    <div class=\"container bodyuser\">\r\n    <p class=\"descrip\">\r\n        <li>Pour accéder au modèle de la charte de qualité : <a href=\"assets/CharteQualiteCNEUF-2019.pdf\" target=\"blank\"> Cliquer ici <i class=\"fas fa-external-link-alt\"></i> </a></li>\r\n        <li>Pour accéder au modèle de cadre logique : <a href=\"assets/CadreLogiqueCNEUF_Indicatif_2019.pdf\" target=\"blank\"> Cliquer ici <i class=\"fas fa-external-link-alt\"></i> </a></li>\r\n        <li>Pour accéder au guide d’utilisation de la plateforme : <a href=\"assets/GuidePréliminaireD_usageCNEUF.pdf\" target=\"blank\"> Cliquer ici <i class=\"fas fa-external-link-alt\"></i> </a></li>\r\n    </p>\r\n    </div>\r\n</div>\r\n\r\n\r\n<app-footer></app-footer>\r\n");

